
.hidden {
  display: none;
}

ul {
  padding-left: 4%;
  padding-right: 4%;
}

.topic {
  display: grid;
  align-items: baseline;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr auto auto;
  flex-wrap: wrap;
  border-style: solid;
  border-color: #8A9968;
  border-width: 1px;
  border-radius: 4px;
  margin-top: 2%;
  padding: 2%;
}

.topic h3 {
  grid-column: 1 / span 4;
  grid-row: 1 / span 1;
}

.topic p {
  grid-column: 5 / span 1;
  grid-row: 1 / span 1;
  justify-self: end;
}

.add-vote{
  font-size: 1rem;
  background-color: #C63B3C;
  color: white;
}

.add-vote:hover{
  font-size: 1rem;
  background-color: #8A9968;
  color: white;
}

.btn{
  margin: 3px;
}
.btn-main {
  background-color: #8E8076;
}

.topicFlex{
  display: flex;
  flex-wrap: wrap;
}

.view-create-buttons {
  display: flex;
  align-self: baseline;
  grid-column: 1 / span 6;
  justify-self: start;
  flex-wrap: wrap;
}

.create-button {
  grid-column: 4 / span 3;
  justify-self: start;
}

.question-list {
  grid-column: 1 /span 6;
  display: none;
}

.check {
  grid-column: 5 / span 2;
  grid-row: 1 / span 1;
  justify-self: end;
}

@media only screen and (max-width: 575.9px){

  .topic p {
    grid-column: 1 / span 2;
    grid-row: 2 / span 1;
    justify-self: start;
  }

  .view-create-buttons{
    width: 100%;
  }

}