.main-wrap {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr 1fr 1fr 2fr;
    grid-template-areas:
      "title"
      "mainTop"
      "mainSearchBar"
      "mainBottom";
  }

 
.main-search{
  grid-area: mainSearchBar;
  justify-content: center;
  margin-left: 7%;
  margin-right: 7%;
  background-color: white;
}

#mainSearch{
  margin: 4%;
}

@media only screen and (max-width: 576px){
  .main-search{
    margin: 0;
    max-width: 100%;
  }
}

@media (min-width: 576px) and (max-width: 991.98px){
  .main-search{
    margin-left: 2%;
    margin-right: 2%;
  } 
}