body {
  background-color: #f7f1f0;
  font-family: 'Roboto', sans-serif;
  color: #0a0908;
}
img {
  max-width: 100%;
  height: auto;
}
.small p{
  margin: 1rem;
  text-align: center;
  font-weight: 500;
}
.small{
  display: none;
}
.Login {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 5%;
  }
  
  .Login form input{
  width: 100%;
  padding: .9rem 1.4rem;
  margin: 8px 0;
  box-sizing: border-box;
  }
  
  .Login form button[type=submit] {
    background-color: #8E8076;
    color: white;
    width: 100%;
    padding: 16px 32px;
    font-size: 1.15rem;
    border-radius: 8px;
    box-shadow:0 6px 20px 0 rgba(0,0,0,0.19);
  }
  
  .Login form button[type=submit]:hover {
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
  }
  
  .Login form button[type=submit]:hover {
    background-color: #afa49d; /*afa49d*/
    color: white;
  }
  
  .Login form a {
    float: right;
  }
  
  .Login form label {
    font-size: 1.15rem;
  }
  
  .btn {
    margin-top: 1rem;
    background-color: #8A9968;
    padding: .4rem 32px;
    font-size: 1.1rem;
  }
  
  .btn:hover {
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
  }
  .btn:hover {
    background-color: #C63B3C;
    color: white;
  
  }#GoogleLoginButton {
    margin-bottom: 1rem;
  }
  
  /* Shared */
  .loginBtn {
    box-sizing: border-box;
    position: relative;
    margin: 0.2em;
    /* padding: 0 15px 0 46px; */
    border: none;
    text-align: left;
    line-height: 34px;
    white-space: nowrap;
    border-radius: 0.3em;
    font-size: 16px;
    color: #FFF;
    box-shadow:0 6px 20px 0 rgba(0,0,0,0.19);
  }
  .loginBtn:before {
    content: "";
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    width: 34px;
    height: 100%;
  }
  .loginBtn:focus {
    outline: none;
  }
  .loginBtn:active {
    box-shadow: inset 0 0 0 32px rgba(0,0,0,0.1);
  }
  
  
  /* Facebook */
  .loginBtn--facebook {
    background-color: #4C69BA;
    background-image: linear-gradient(#4C69BA, #3B55A0);
    text-shadow: 0 -1px 0 #354C8C;
    padding: 0 15px 0 46px;
  }
  .loginBtn--facebook:before {
    border-right: #364e92 1px solid;
    background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/14082/icon_facebook.png') 6px 6px no-repeat;
  }
  .loginBtn--facebook:hover,
  .loginBtn--facebook:focus {
    background-color: #5B7BD5;
    background-image: linear-gradient(#5B7BD5, #4864B1);
  }
  
  
  /* Google */
  .loginBtn--google {
    background: #DD4B39;
    padding: 0 33px 0 45px;
  }
  .loginBtn--google:before {
    border-right: #BB3F30 1px solid;
    background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/14082/icon_google.png') 6px 6px no-repeat;
  }
  .loginBtn--google:hover,
  .loginBtn--google:focus {
    background: #E74B37;
  }
  
  .btnLink
  {
    background:none!important;
    border:none; 
    padding:0!important;
    color:#069;
    text-decoration:underline;
    cursor:pointer;
  }

  .btnLink:hover
  {
    background:none!important;
    border:none; 
    padding:0!important;
    color:blue;
    text-decoration:underline;
    cursor:pointer;
  }

 
@media only screen and (max-width: 575.9px){
  .large{
    display: none;
  }
  .small{
    display: inline-block;
    align-content: center;
    justify-content: center;
    /* text-align: center; */
    font-size: 1rem;
  }
  .Login form button[type=submit] {
    background-color: #8E8076;
    color: white;
    width: 100%;
    padding: .4rem 32px;
    font-size: 1.1rem;
    border-radius: 8px;
    box-shadow:0 6px 20px 0 rgba(0,0,0,0.19);
    
  }
  .loginBtn{
    width: 100%;
    text-align: center;
    margin-top: .2rem;
    margin-left: 0;
  }
   .createAccount{
     width: 100%;
   }
}
