#manageStudentsModal
{
    flex-direction: column;
    background: white;
    align-items: center;
}

#manageStudentsModal h1
{
    text-align: center;
    padding-top: .5em;
    padding-bottom: .5em;
}

#manageStudentsModal li
{
    list-style-type: none;
    padding: .5em 0;
}

#manageStudentsModal section
{
  display: flex;
  flex-direction: column;
  align-items: center;
}

#manageStudentsModal input
{
  margin: 0px;
  padding: .5em 0;
}

#requestingApprovalList,
#studentsEnrolledList
{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 0px;

}

#manageStudentsModal span
{
  align-self: flex-end;
  padding-right: 1rem;
}

#manageStudentsModal input
{
  width: 100%;
  box-sizing: border-box;
}

#manageStudentsModal p
{
  font-weight: bold;
  text-align: center;
  padding-top: 2em;
}

.approve
{
  background-color: #8A9968;
  color: white;
  cursor: pointer;
}

.deny
{
  background-color: #8E8076;
  color: white;
  cursor: pointer;
}

.delete-student
{
  background-color: #C63B3C;
  color: white;
  cursor: pointer;
}


