.title{
  grid-area: title;
  background-color: #8A9968;
  display: grid;
  margin-left: 7%;
  margin-right: 7%;
  margin-top: 5%;
  padding: 3%;
  justify-content: space-around;
  align-items: center;
}

.header {
  grid-area: mainTop;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
  margin-right: 7%;
  margin-left: 7%;
  background-color:  #fff;
  padding-left: 2%;
  padding-right: 2%;
  /* padding-bottom: 1%; */
  max-width: 86%;
}


.headerButtons{
  grid-row: 1/2;
  display: flex;
  justify-content: center;
  padding: 3%;
  flex-wrap: wrap;
}

h1 {
  grid-area: title;
  grid-column: 1/2;
  grid-row: 1;
  display: flex;
  justify-content: center;
  align-content: flex-start;
}

h2 {
  grid-row: 3/4;
  display: flex;
  justify-content: center;
  align-content: flex-start;
}

.btn-main {
  background-color: #8E8076;
}

 .addTopicForm {
  grid-row: 2 / 3;
  justify-content: center;
  padding: 3%;
  flex-wrap: wrap;
 }

 .addTopicInput{
  max-width: 100%;
  /* margin: 2%; */
 }

 .addTopicBtn{
  margin: 2%;
 }

@media only screen and (max-width: 575.9px){
  .header{
    margin: 0;
    max-width: 100%;
    padding: 0;
  }
  .title{
    margin: 0;
    padding-top: 10%;
    padding-bottom: 10%;
  }

  .btn-header{
      width: 100%;
  }
  .addTopicBtn{
    width: 100%;
    margin: 2%;
   }
  .addTopicInput{
    max-width: 100%;
    margin: 0;
   }
}

@media (min-width: 576px) and (max-width: 991.98px){
  .header{
    margin-left: 2%;
    margin-right: 2%;
    max-width: 96%;
  } 
  .title{
    margin-left: 2%;
    margin-right: 2%;
  }    
}