@keyframes modalFade
{
  from {transform: translateY(0);opacity: 0;}
  to {transform: translateY(0);opacity: 1;}
}


.Modals {
  position: fixed;
  top: 0;
  left: 0;
  /* display: none; */
  /* display: flex; */
  z-index: 100;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  animation-name: modalFade;
  animation-duration: .5s;
  background-color: rgba(105,105,105,0.5);
  }
  
