#createAQuestionModal {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: white;
}
#createAQuestionModal h1 {
  margin: 1rem;
}

#createAQuestionModal section form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

#createAQuestionModal textarea {
  grid-row: 1 / 3;
  grid-column:  1 / 4;
}

#createAQuestionModal section input[type=submit] {
  background-color: #8A9968;
  color: white;
  padding: 10px 32px;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 8px;
  box-shadow:0 6px 20px 0 rgba(0,0,0,0.19);
}

#createAQuestionModal form input[type=submit]:hover {
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
}

#createAQuestionModal form input[type=submit]:hover {
  background-color: #afa49d; /*afa49d*/
  color: white;
}

#createAQuestionModal span {
    align-self: flex-end;
    padding-right: 1rem;
}