.listPane{
    grid-area: mainBottom;
    background-color: white;
    margin-left: 7%;
    margin-right: 7%;
    margin-bottom: 7%;
}


.listPane ul{
    color: black;
    font-size: 1rem;
}

.listPane li:hover{
    background-color: #F7F1F0;
}

@media only screen and (max-width: 575.9px){
    .listPane{
      margin: 0;
      max-width: 100%;
    }
}

@media (min-width: 576px) and (max-width: 991.98px){
    .listPane{
      margin-left: 2%;
      margin-right: 2%;
      max-width: 96%;
    }  
  }