.main-wrap {
  background-color: #F7F1F0;
    position: absolute;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-bottom: 7%;
    -moz-transition: left 0.5s ease;
    transition: left 0.5s ease;
}

.sidebar {
    background-color: #8A9968;
    width: 20%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    /* bottom: 0; */
    display: none;
}


#sideBarLabel{
  position: fixed;
  z-index: 1;
  text-decoration: none;
  font-size: 1rem;
  color: white;
  background-color: #8A9968;
  padding: 10px;
  top: 35%;
  left: -2.8rem;
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
}

.sidebar .teacher-student-badge{
  display: none;
}

.closeSideBar{
  position: absolute;
  top: 10px;
  right: 10px;
  color: white;
  size: .5rem;
  cursor: pointer;
}

.sidebar h2 {
  color: #ffffff;
  list-style-type: none;
  font-size: 2rem;
  cursor: pointer;
  text-align: center;
  padding-top: 2rem;
  align-items: center;
  align-self: stretch;
  margin-top: 10%;
}

.sidebar ul{
  margin-top: 10%;
  align-items: stretch;
  padding-left: 0;
  padding-right: 0;

}
.sidebar li {
  display: block;
  color: white;
  font-weight: 200;
  list-style-type: none;
  font-size: 1rem;
  cursor: pointer;
  padding-top: .5rem;
  padding-bottom: .5rem;
  text-align: center;
}

.sidebar li:hover {
  background-color: #68734e;
}

input[type=checkbox]#slideSidebar {
   display: none;
}

input#slideSidebar:checked ~ .main-wrap {
    left: 20%;
}

input#slideSidebar:checked ~ #sideBarLabel {
    display: none;
}

input#slideSidebar:checked ~ .sidebar {
    display: block;
}


  @media screen and (max-width: 575.9px){
  #sideBarLabel{
    display: none;

  }
  input#slideSidebar:checked ~ .main-wrap {
      display: none;
      position: fixed;
      top: 0;
      left: -20%;
      -moz-transition: right 0.5s ease;
      transition: right 0.5s ease;
  }
  .sidebar {
      width: 100%;
  }
  .sidebar li{
      font-size: 1.7rem;
  }
  .sidebar h2{
      font-size: 3rem;
    }
    
  }

  @media (min-width: 576px) and (max-width: 991.98px){
    .sidebar {
      width: 25%;
    }
    input#slideSidebar:checked ~ .main-wrap {
      left: 25%;
    }
    #sideBarLabel{
      display: none;
    }
  }