.AccountManagementModal
{
    background: white;
}

.AccountManagementModal h1
{
    text-align: center;
    padding: .5em 0;
}

.AccountManagementModal form
{
    display: flex;
    flex-direction: column;
}


.AccountManagementModal form input[type=email], input[type=text], input[type=password]
{
    width: 100%;
    margin-bottom: 1em;
}

.AccountManagementModal form input[type=submit]
{
    background-color: #8A9968;
    color: white;
    cursor: pointer;
    font-size: 1.15rem;
    width: 50%;
    align-self: center;
    border-radius: 8px;
    box-shadow:0 6px 20px 0 rgba(0,0,0,0.19);
}


.AccountManagementModal span
{
    align-self: flex-end;
    padding-right: 1rem;
}

.AccountManagementModal section {
    text-align: center;
}


.AccountManagementModal form
{
    padding: 0 3em 1em 3em;
}

.AccountManagementModal .close {
    border-top: 10%;
    align-self: center;
    margin-top: 1em;
}
