/*---global---**/
#CreateAccountModal
{
  background: white;
  /* padding: 2%; */
}

/*---header---*/
#CreateAccountModal span
{
  align-self: flex-end;
}

#CreateAccountModal h1
{
text-align: center;
/* padding-top: 2%; */
padding-bottom: 5%;
}

/*---sign up form---*/

#CreateAccountModal p {
  text-align: center;
  padding-top: 2%;
}
#CreateAccountModal form
{
display: flex;
flex-direction: column;
}

/*---submit button---*/
#CreateAccountModal form input[type=submit]
{
background-color: #8A9968;
color: white;
cursor: pointer;
font-size:x 1.15rem;
font-weight: 700;
padding: 1rem;
margin-top: 2rem;
}

/* cancel link */
.cancel {
  color:#069;
  margin-top: 1rem;
}

.cancel:hover {
  color:blue;
}

#fb-google-buttons
{
  align-content: center;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
 }

#fb-google-butons button {
  margin-top: 5%;
  margin-bottom: 7%;
}
 
.noMatch {
  background-color: lightpink;
}

@media screen and (max-width: 360px){
  .our-modal
  {
    width: 80%;
    height: 100vh;
    line-height: 1em
  } 
}