
#createAClassModal
{
  /* display: flex;
  flex-direction: column;
  align-items:center; */
  flex-wrap: wrap;
  background: white;
  max-width: 451px;

}

#createAClassModal input[type=date]
{
  width: 40%;
}

#createAClassModal .close
{
  align-self: flex-end;
}

#days, #time-from, #pvt
{
  margin: .5% 0 0 0;
}

.date-from, .time-from
{
  display: flex;
  flex-direction: row;
  justify-content: space-between; 
}

 #createAClass p 
{
margin-bottom: -.5%;
 
}

/*---Styling for days of week input---*/
.days-container
{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 2%;
}

#createAClassSubmit input[type=submit] {
    background-color: #8A9968;
    padding: 5px 32px;
    cursor: pointer;
    font-size: 1rem;
    width: 100%;
}

#createAClass input[type="checkbox"]
{
  margin-right: 5px;
}

#createAClassModal input[type=date]
{
  width: 45%;
}

@media screen and (max-width: 450px) 
{
  #createAClassModal
  {
    background-color: blue;
    width: 100vw;
    height: 100vh;
    padding: 0 5%;
  }

  .date-from
  {
    display: flex;
    flex-direction: column;
    justify-content: space-between; 
  }

  #createAClassModal input[type=date]
{
  width: 100%;
}

.date-from p 
{
  margin: 0 auto;
}

  .days-container
{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1%;
}



}